@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: block;
  font-family: 'Poppins', sans-serif;
  color: #102a42;
  background: #4b145b;
  font-size: .875rem;
  line-height: 1.5;
}

.main-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: grid;
  grid-gap: 1rem 2rem;
  gap: 1rem 2rem;
  max-width: 920px;
  width: 90vw;
  padding: 2.5rem 2rem;
  justify-content: center;
  margin: 5rem auto;
  background-color: #fff;
  border: none;
  border-radius: 0.25rem;
}

.container h3{
  line-height: 1.2;
  font-weight: bold;
}

.question {
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  border: 2px solid #eae6eb;
  border-radius: 0.25rem;
  box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
}

.question header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.question header h4 {
  margin-bottom: 0;
}

.question h4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: none;
  line-height: 1.5;
}

.btn {
  background: transparent;
  border-color: transparent;
  width: 2rem;
  height: 2rem;
  background: #eae6eb;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: #b4345c;
  cursor: pointer;
  margin-left: 1rem;
  align-self: center;
}

@media screen and (min-width: 800px) {
  body, h4 {
      font-size: 1rem;
  }
}

@media screen and (min-width: 992px) {
  .container {
    display: grid;
    grid-template-columns: 250px 1fr;
  }
}

@media screen and (min-width: 800px) {
  h1, h2, h3, h4 {
      line-height: 1;
  }
}

@media screen and (min-width: 800px) {
  h3 {
      font-size: 1.75rem;
  }
}